.container.testimonials_container{
    width: 40%;
    padding-bottom: 2rem;
    margin-bottom: 5rem;
}
.client_avatar{
        width: 4rem;
        aspect-ratio: 1/1;
        overflow: hidden;
        border-radius: 50%;
        margin: 0 auto 1rem;
        border: 0.4rem solid var(--color-primary-variant)
}
.testimonial {
            background: var(--color-bg-vaiant);
            text-align: center;
            padding: 2rem;
            border-radius: 2rem;
            user-select: none;
}

.client_review {
    color: var(--color-light);
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;
}
/* =====================MEDIA QUERIES========================== */
@media screen and (max-width: 1024px) {
    .container.testimonials_container {
        width:60%;
    }
}
/*========= MEDIA QUERIES (SMALL DEVICE======================= */
@media screen and (max-width: 600px) {
    .container.testimonials_container {
        width: var(--container-width-sm);
    }
        .client_review {
        width: var(--container-width-sm);
        }
}